.logo {
  display: flex;
  justify-content: space-between;
  position: relative;
  left: 15px;
}

.topbar ul {
  list-style: none;
}

.topbar li {
  float: left;
  border: 1px solid black;
  padding-block: 10px;
  padding-inline: 30px;
  text-align: center;
  position: relative;
  top: 10px;
  right: 20%;
}

.body {
  display: flex;
  justify-content: space-around;
  padding-top: 30px;
  margin: auto;
}

.sidebar {
  flex: 0.1;
  position: relative;
  align-items: center;
  text-align: center;
  min-height: 800px;
  min-width: 300px;

}

.bottomButton {
  position: absolute;
  bottom: 10px;
  left: 20px;
}

.bottomButton button {
  align-self: center;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 25px;
  padding-right: 25px;
  font-size: 20px;
}

.main {
  display: flex;
  flex: 0.8;
  position: relative;
  justify-content: normal;
  text-align: center;
  min-height: 800px;
}

.profile {
  height: 300px;
  width: 300px;
  margin: 20px;
}

.profile img {
  height: 150px;
  width: 100px;
  padding: 10px;
}

.profile .person {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.profile Button {
  padding: 10px;
  margin-block: 20px;
}